import React, { Component, memo, createRef } from "react";
import { isTagShop } from "../../../actions/api";
import { connect } from "react-redux";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide'; 
import { Grid } from '@splidejs/splide-extension-grid';
import Card from "./Elements/Card";
import "./scss/HighlightTheme.scss";
import '../scss/ThemesSplide.scss'
import { nextData } from "../../../actions/moredataAction";

class HighlightTheme extends Component {
  constructor(props) {
    super(props)
    this.state = { loadData : false }
    this.inputRefs = {};
    this.isDragging = false; // Class property to track dragging

  }
  requestData = () => {
    const { appendData, ThemeInfo, ThemeStyle, preRender, hasMoreData, nextData } = this.props;
    !this.state.loadData && this.updatepost(0) 
    if (hasMoreData) {
      nextData(
        isTagShop ? ThemeInfo.webId : ThemeInfo.wallId,
        Math.floor(Date.now() / 1000),
        ThemeStyle.totalPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };


  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole && classNameRole.forEach((element) => {element.removeAttribute("aria-hidden");element.removeAttribute("role")});
    }, 100);
  }


  updatepost = (e) => {    
    if(!this.state.loadData){ 
      this.setState({ loadData : true })
      setTimeout(() => this.props.loadMoredata() , (e*1000))
    }
}


  componentDidUpdate() {
    const value = Object.values(this.inputRefs)[0];
    const { postData, completeDataObject, onClickPopUpSlider, ThemeStyle, parentID } = this.props;
    const rowCount = ThemeStyle.row;
    if (value && Object.values(value)[5].length > 0) {
      let data = Object.values(value)[5]
      data.map((item) => {
        if (item.classList) {
          const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
          var getID = filterID[0]
          if (String(filterID[0]).includes("tb_")) {
            getID = String(filterID[0]).replace("tb_", "")
          }
          const findIndex = postData.findIndex((find) => find == getID)
          let clickD = document.querySelectorAll(`#${item.id}`)
          for (let index = 0; index < clickD.length; index++) {
            let clickDD = clickD[index];
            let p_id = clickDD.getAttribute('p_id')
            if (String(parentID) === String(p_id)) {
              clickDD.addEventListener('click', event => {
                event.preventDefault();
                const itemData = completeDataObject[getID]
                onClickPopUpSlider(findIndex, itemData)
              });
            }
          }
        }
      })
    }
    else if (rowCount == 2) {
      let data = Object.values(value)[5]
      data.map((item) => {
        if (item && item.childNodes.length > 0) {
          item.childNodes.forEach((childItem) => {
            if (childItem && childItem.childNodes.length > 0) {
              childItem.childNodes.forEach((subChildItem) => {
                if (subChildItem.classList) {
                  const filterID = String(subChildItem.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
                  var getID = filterID[0]
                  if (String(filterID[0]).includes("tb_"))  getID = String(filterID[0]).replace("tb_", "")
                  const findIndex = postData.findIndex((find) => find == getID)
                  let clickD = document.querySelectorAll(`.tb_hs_post_container #${subChildItem.id}`)
                  for (let index = 0; index < clickD.length; index++) {
                    let clickDD = clickD[index];
                    let p_id = clickDD.getAttribute('p_id')
                    if (String(parentID) === String(p_id)) {
                      clickDD.addEventListener('click', event => {
                        event.preventDefault();
                        const itemData = completeDataObject[getID]
                        onClickPopUpSlider(findIndex, itemData)
                      });
                    }
                  }
                }
              })
            }
          });
        }
      })
    }
  }

  autoScrollSlider = (splide) => {
  
    // Detect user drag (touch move)
      splide.on("drag", () =>{ 
        this.isDragging = true; // Reset flag after move
        !this.state.loadData && this.updatepost(0)
      } );

      // Listen for auto move (autoplay)
      splide.on("move", () => { !this.state.loadData && !this.isDragging && this.updatepost(3)} );

     const currentIndex = splide.index
    const slideLength = splide.length
    const permove = splide["_o"].perPage
    if (slideLength - currentIndex === permove) this.requestData()
  }

  getPerPage = (defaultValue) => {
    const mobileColumn = this.props.ThemeStyle.column.mobile;
    return isTagShop ? this.props.cardNumber : (mobileColumn > 0 ? mobileColumn : defaultValue);
  }

  render() {
    const { postData, completeDataObject, CardStyle, ThemeStyle, ThemeInfo, clickToShowPopUp, parentID, onClickToCTA, cardNumber,  onClickPopUpSlider } = this.props;
    const numberOfCoumn = ThemeStyle.column.desktop;
    const slidePost = ThemeStyle.slide.autoSlide;
    const slideDuration = ThemeStyle.slide.slideDuration;
    const autoScrollStatus = ThemeStyle.autoScrollStatus;
    const inputName = parentID;
    const rowCount = 1;
    const cloneStatus = {};
    const isType = autoScrollStatus !== 1 && Object.keys(cloneStatus).length ? false : 'slide';
    const settings = {
      ...cloneStatus,
      grid: {
        rows: rowCount,
      },
      type: autoScrollStatus === 1 ? false : isType,
      rewind: true,
      rewindSpeed: 1000,
      interval: slidePost === 1 && parseInt(slideDuration) > 0 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: isTagShop ? cardNumber : numberOfCoumn > 0 ? numberOfCoumn : 5,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      arrows: true,
      breakpoints: {
        560: { perPage: this.getPerPage(1) },
        767: { perPage: this.getPerPage(2) },
        991: { perPage: this.getPerPage(3) },
        1200: { perPage: isTagShop ? cardNumber : (numberOfCoumn > 0 ? numberOfCoumn : 4) },
        1400: { perPage: isTagShop ? cardNumber : (numberOfCoumn > 0 ? numberOfCoumn : 5) },
        1600: { perPage: isTagShop ? cardNumber : (numberOfCoumn > 0 ? numberOfCoumn : 6) },
      }
    };

    const totalPost = 10;
    let newPostdata = postData.length < totalPost 
      ? Array.from({ length: totalPost }, (_, i) => postData[i % postData.length]) 
      : postData;

    return (parentID != "" ?
      <div className="tb_ht_post_container">
        <Splide hasTrack={false} className="tb_ht_post_slider" options={settings} extensions={{ Grid }} ref={(parentID) => this.inputRefs[inputName] = parentID} onMove={this.autoScrollSlider}>
          <SplideTrack>
            {
              (ThemeStyle.row > 1 && newPostdata.length !== 1 && newPostdata.length % 2 !== 0 ? newPostdata.pop() : newPostdata) && ((newPostdata && newPostdata.length > 0)) && newPostdata.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide key={index} style={{ margin: 0, padding: 0 }} className={`tb_${cardData.id}`} p_id={parentID}>
                  <Card clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo}/>
                </SplideSlide>
              })}
          </SplideTrack>
          <div className="splide__arrows splide__arrows--ltr tb_ht_arrow_wrapper_">
            <div className="tb_ht_arrow splide__arrow splide__arrow--prev tb_ht_arrow tb_ht_arrow_left__ tb__icon tb-arrow-left-alt" role="button" aria-label="Go to previous slide"><div></div></div>
            <div className="tb_ht_arrow splide__arrow splide__arrow--next  tb_ht_arrow_right__ tb__icon tb-arrow-right-alt" onClick={()=>!this.state.loadData && this.updatepost(0) } role="button" aria-label="Go to next slide"><div></div></div>
          </div>
        </Splide>
      </div> : null
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    nextData: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(nextData(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default memo(connect("", mapDispatchToProps)(HighlightTheme));
