import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import { isTagbox } from "../../../../actions/api";
const Media = ({ Post, ThemeInfo, ThemeStyle, ThemeRule, index }) => {
  const size =  ThemeInfo?.productType == 9 || isTagbox ? ThemeRule.aspectImageRatio ? ThemeRule.aspectImageRatio : 100 : 100;
  const isVideo = ([3, 5].includes(Post.type)) && ThemeStyle.autoPlay == 1 ? true : false;
  return (
    <div className="tb_ht_media_wrap">
      <MediaIcon Post={Post} IconClass="tb_ht_media_icon"  isCenter={false} />
      {isVideo ? (
        <Video VideoClass="tb_ht_video" Post={Post} size={size} isCover={true} controls={false} autoPlay={true} muted={true} />
      ) : (
        <Image ImageClass="tb_ht_image" Post={Post} size={size} hotspot={false} index={index}/>
      )}
    </div>
  );
};
export default Media;
